import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import {
  itemRemovedFromFavs,
  itemAddedToCart } from '../../actions';


import './favs-modal.sass';

const FavsModal = ({ items, onRemovedFromFavs, onAddedToCart }) => {
  const renderRow = (item, idx) => {
    const { id, code, title, pictureUrl, price } = item;

    return (
      <li key={idx} className="favs-item">
        <div className="preview-img">
          <img
            src={pictureUrl}
            alt="product"/>
        </div>
        <div className="preview-body">
          <div className="preview-text">
            <Link to={'/item-page/' + code}>{title}</Link>
          </div>
          <span className="preview-price">{price} грн </span>
        </div>
        <div className="preview-actions">
          <button
            onClick={() => onRemovedFromFavs(id)}
            className="btn-clear remove-fav">
          </button>
          <button
            className="btn-clear fav-buy"
            onClick={() => onAddedToCart(id)}>
            Добавить в корзину
          </button>
        </div>
      </li>
    );
  };


  return (
    <div className="cart-modal-window">
      <h2>Избранное</h2>
      {items.map(renderRow)}
    </div>
  );
};

const mapStateToProps = ({ favsList: { favsItems }}) => {
  return {
    items: favsItems
  };
};

const mapDispatchToProps = {
  onRemovedFromFavs: itemRemovedFromFavs,
  onAddedToCart: itemAddedToCart,
};


export default connect(mapStateToProps, mapDispatchToProps)(FavsModal);
