import React, {Component} from 'react';

import { Link } from 'react-router-dom';
// import firebase from '../../firebase.js';

import Header from '../header';

import './item-page.sass';

import { connect } from 'react-redux';
import { withStoreService } from '../hoc';
import { fetchItems, itemAddedToCart, itemAddedToFavs } from '../../actions';
import { compose } from '../../utils';


class ItemPage extends Component {

  constructor() {
    super();
    this.state = {
      item: [],
      dropdownIsHidden: true
    };
  }

  componentDidMount() {
    // this.props.fetchItems();
    let itemId = this.props.match.params.item_id;
    console.log('here is itemid ' + itemId);
    let item = this.findItem(this.props.items, itemId);
    this.setState({
      item: item
    });
  }

  findItem(items, code) {
    if (!code) return [];
    const result = items.find(item => item.code === code);
    return result;
  }

  updateData = (value) => {
     this.setState({ dropdownIsHidden: value })
  }

  translateCategory = (categoryId) => {
    switch (categoryId) {
      case 'smartphones':
        return 'Смартфоны';
      case 'notebooks':
        return 'Ноутбуки';
      case 'watches':
        return 'Смарт-часы';
      case 'audio':
        return 'Аудио';
      case 'fitness':
        return 'Фитнес и здоровье';
      case 'tv':
        return 'Телевизоры';
      case 'accessories':
        return 'Аксессуары';
      default:
        return 'Категория';
    }
  }


  render() {

    const { id, code, title, price, pictureUrl, category, brand } = this.state.item;

    const categoryH2 = this.translateCategory(category);

    return (
      <div>
        <Header updateData={this.updateData}/>
        <div className="page-wrapper">
          <main className="page">
            <div className="col-12">
              <aside className="item-navigation">
                <nav className="breadcrumbs">
                  <ul>
                    <li><Link to="/"><img src={require('../../assets/home.svg')} alt="home"/></Link></li>
                    <li><Link to={'/shop-page/' + category}>{categoryH2}</Link></li>
                    <li><Link to="#">{brand}</Link></li>
                  </ul>
                </nav>
                <span className="item-code">
                  Код: {code}
                </span>
              </aside>
              <article className="item-content row">
                  <div className="col-6">
                    <div className="img-wrapper">
                      <img
                        src={pictureUrl}
                        alt="product"/>
                    </div>
                  </div>
                  <div className="col-6">
                    <div className="item-text">
                      <h1>{title}</h1>
                      <div className="options-picker">
                        <div className="color-picker">
                          <p>Цвет</p>
                          <div className="custom-color-picker">

                            <div>
                              <input type="radio" id="color-1" name="color" value="color-1" />
                              <label htmlFor="color-1">
                                <span></span>
                              </label>
                            </div>
                            <div>
                              <input type="radio" id="color-2" name="color" value="color-2" />
                              <label htmlFor="color-2">
                                <span></span>
                              </label>
                            </div>
                            <div>
                              <input type="radio" id="color-3" name="color" value="color-3" />
                              <label htmlFor="color-3">
                                <span></span>
                              </label>
                            </div>

                          </div>
                        </div>
                        <div className="model-picker">
                          <p>Модель</p>
                          <div className="custom-model-picker">

                            <div>
                              <input type="radio" id="color-4" name="color" value="color-4" defaultChecked />
                              <label htmlFor="color-4">
                                <span>256 Гб</span>
                              </label>
                            </div>
                            <div>
                              <input type="radio" id="color-5" name="color" value="color-5" />
                              <label htmlFor="color-5">
                                <span>512 Гб</span>
                              </label>
                            </div>

                          </div>
                        </div>
                      </div>
                      <span className="price">{price} грн</span>
                      <div className="buttons-row">
                        <button
                          className="buy-success btn-clear"
                          onClick={() => this.props.onAddedToCart(id)}>
                          Купить
                        </button>
                        <button className="buy-secondary btn-clear">Купить в кредит</button>
                        <button
                          className="favs-add btn-clear"
                          onClick={() => this.props.onAddedToFavs(id)}>
                        </button>
                      </div>
                      <div className="divider"></div>
                      <form className="fast-buy">
                        <h3>Быстрый заказ</h3>
                        <span>Номер телефона:</span>
                        <input type="text" name="number" placeholder="+38 0XX XXX XX XX" required />
                        <span>Комментарий (по желанию):</span>
                        <input type="text" name="comment" placeholder="хочу золотой макбук!" />
                        <button className="buy-now btn-clear">Оформить заказ</button>
                      </form>
                    </div>
                  </div>
              </article>
            </div>
          </main>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ itemsList: { items, loading, error }}) => {
  return { items, loading, error };
};

const mapDispatchToProps = (dispatch, { storeService }) => {

  return {
    fetchItems: fetchItems(storeService, dispatch),
    onAddedToCart: (id) => dispatch(itemAddedToCart(id)),
    onAddedToFavs: (id) => dispatch(itemAddedToFavs(id)),
  };
};

export default compose(
  withStoreService(),
  connect(mapStateToProps, mapDispatchToProps)
)(ItemPage);
