import React, {Component} from 'react';

import './selected.sass';


export default class Selected extends Component{

  render() {

    return (
      <aside className="sidebar">
        <div className="selected-header">
          <h3>Вы выбрали:</h3>
          <button className="btn-clear delete-all-selected">
            <span>Очистить всё</span>
          </button>
        </div>
        <div className="selected-item">
          <span>Цена:</span>
          <span>от 30000 до 43568</span>
          <button className="btn-clear delete-selected"></button>
        </div>
        <div className="selected-item">
          <span>Бренд:</span>
          <span>Apple</span>
          <button className="btn-clear delete-selected"></button>
        </div>
        <div className="selected-item">
          <span>Объем памяти: </span>
          <span>512 Гб</span>
          <button className="btn-clear delete-selected"></button>
        </div>

      </aside>
    );
  }
}
