import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// import firebase from '../../firebase.js';
import SearchPanel from '../search-panel';
import CartModal from '../cart-modal';
import FavsModal from '../favs-modal';
import Modal from 'react-modal';

import { connect } from 'react-redux';
import { withStoreService } from '../hoc';
import { fetchItems } from '../../actions';
import { compose } from '../../utils';

import './header.sass';


const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
    borderRadius          : '8px',
    border                : '1px dashed #33cc33',
    padding               : '20px 30px'
  }
};

Modal.setAppElement('#root')


class Header extends Component {

  constructor() {
    super();
    this.state = {
      cartModalIsOpen: false,
      favsModalIsOpen: false,
      term: '',
      dropdownIsHidden: false
    };

    this.openCartModal = this.openCartModal.bind(this);
    this.closeCartModal = this.closeCartModal.bind(this);
    this.openFavsModal = this.openFavsModal.bind(this);
    this.closeFavsModal = this.closeFavsModal.bind(this);
  }

  //modals
  openCartModal() {
    this.setState({cartModalIsOpen: true});
  }

  closeCartModal() {
    this.setState({cartModalIsOpen: false});
  }

  openFavsModal() {
    this.setState({favsModalIsOpen: true});
  }

  closeFavsModal() {
    this.setState({favsModalIsOpen: false});
  }
  //end of modals

  toggleMenu = () => {
    this.setState({ dropdownIsHidden: !this.state.dropdownIsHidden });
    this.props.updateData(this.state.dropdownIsHidden);
  }

  //search
  onSearchChange = (term) => {
    this.setState({ term });
  };

  search(items, term) {
    if (!term) return [];
    let searched = items.filter((item) => {
      return item.title.toLowerCase().indexOf(term.toLowerCase()) > -1;
    });
    return searched;
  }
  //search end (plus in render calling a func)

  render() {

    console.log(this.props.items);
    let searchedItems = this.search(this.props.items, this.state.term);
    console.log(searchedItems);


    return (
      <div>
        <header>
          <div className="stretching">
            <Link to="/admin-page">акция распродажа смотреть сюда!</Link>
          </div>
          <div className="primary">
            <div className="inner-primary">
              <Link to="/" className="logo">
                New Project
              </Link>
              <ul>
                <li><Link to="/">Доставка и оплата</Link></li>
                <li><Link to="/">Гарантия</Link></li>
                <li><Link to="/">О нас</Link></li>
              </ul>
              <div className="contacts">
                <a href="tel:'380999999999'">+38 099 99 99 999</a>
              </div>
            </div>
          </div>
          <div className="secondary">
            <div className="secondary-inner">
              <div className="col-3">
                <div
                  className="categories-header"
                  onClick={this.toggleMenu}
                >
                  <span></span>Каталог
                </div>
              </div>
              <div className="col-6">
                <SearchPanel onSearchChange={this.onSearchChange} />
              </div>
              <div className="col-3">
                <div className="favs-and-cart">
                  <button className="favs btn-clear" onClick={this.openFavsModal}></button>
                  <button className="cart btn-clear" onClick={this.openCartModal}></button>
                </div>
              </div>
            </div>
          </div>
        </header>

        <Modal
          isOpen={this.state.cartModalIsOpen}
          onRequestClose={this.closeCartModal}
          style={customStyles}
          contentLabel="Cart Modal"
        >
          <button className="modal-close btn-clear" onClick={this.closeCartModal}></button>
          <CartModal />
        </Modal>

        <Modal
          isOpen={this.state.favsModalIsOpen}
          onRequestClose={this.closeCartModal}
          style={customStyles}
          contentLabel="Favs Modal"
        >
          <button className="modal-close btn-clear" onClick={this.closeFavsModal}></button>
          <FavsModal />
        </Modal>


        <div className="search-block">
          <div className="inner-search">
            <ul className="search-ul">
              {searchedItems.map((item) => {
                return (
                  <li key={item.id} className="search-li">

                    <Link to={'/item-page/' + item.code}>
                      <div className="preview-img">
                        <img
                          src={item.pictureUrl}
                          alt="product"/>
                      </div>
                    </Link>

                    <div className="preview-body">
                      <div className="preview-text">
                        <Link to={'/item-page/' + item.code}>{item.title}</Link>
                      </div>
                      <span className="preview-price">
                        {item.price} грн
                      </span>
                    </div>

                    <Link to={'/item-page/' + item.code}>
                      <button className="btn-clear preview-link"></button>
                    </Link>

                  </li>
                )
              })}
            </ul>
          </div>
        </div>

      </div>
    )
  }
}

const mapStateToProps = ({ itemsList: { items, loading, error }}) => {
  return { items, loading, error };
};

const mapDispatchToProps = (dispatch, { storeService }) => {

  return {
    fetchItems: fetchItems(storeService, dispatch)
  };
};

export default compose(
  withStoreService(),
  connect(mapStateToProps, mapDispatchToProps)
)(Header);
